@import "../../styles/AppDesign.scss";

$topSpace: 25px;
$topPadding: 40px;
$bottomPadding: 60px;
$sideSpace: 30px;
$sideBarWidth:60px;
$topOffset: $topSpace+$topPadding;

.sandtable {
  position: relative;
  display: block;
  height: 100%;

  @media (max-width: 991px) {
    height: auto;
  }

  // h2, h3{
  //   margin: 0 0 10px;
  // }

  select {
    // width:100%;
    background: transparent;
    border: 2px solid #000;
    max-width: 200px;
    text-align: center;
  }
}

.sandtable__head {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: $topPadding;
  height: $topOffset;
  position: relative;
  z-index: 100;

  .is-fullscreen & {
    display:none;
  }
  @media (max-width: 991px) {
    .sandtable__control & {
      display: none;
    }
  }
}

.sandtable__body {
  display: flex;
  align-items: stretch;
  justify-content: stretch;

  width: 100%;
  height: 100%;

  @media (max-width: 991px) {
    display: block;
    height: auto;
  }
}


.sandtable__board,
.sandtable__control {
  position: relative;
  width: 50%;
  height: 100vh;
  flex: 0 0 auto;

  @media (max-width: 991px) {
    display: block;
    width: 100%;
    height: auto;
  }
}

.sandtable__board {
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: $darkGray;

  nav {
    position: absolute;
    top: 50%;
    left: 0;
    height: 0;
    width: 100%;
    display: flex;
    align-items: cener;
    margin-top: 18px;
    display: none;
  }

  .is-halfscreen & {
    padding: $topSpace $sideSpace $bottomPadding;
    box-sizing: border-box;
    @media (max-width: 991px) {
      padding-left: 40px;
      padding-right: 40px;
    }
  }
}

.sandtable__control {
  .is-halfscreen & {
    padding: $topSpace 0;
    box-sizing: border-box;

    @media (max-width: 991px) {
      padding: 0;
    }
  }
  .is-fullscreen & {
    display:none;
  }
}

.sandtable__panel {
  .is-fullscreen & {
    position: fixed;
    right: 0;
    bottom: 0;
    background: rgba(#f8f8f8, 0.8);
    padding: 30px;
  }

  // padding: 0 30px;
}

.sandtable-board {
  position: relative;
  // background:#000;
  width: 100%;
  max-width: 480px;
  margin: 0 auto 30px;
  cursor: grab;
  // padding-top:36px;

  .is-halfscreen & {
    max-width: initial;
    max-width: none;
    width: 100%;
    height: calc(100% - #{$topOffset + $bottomPadding});
    left: 0;
    top: 0;
    padding: 0;
    margin: 0;
    flex: 1 0 auto;
  }

  .is-fullscreen & {
    max-width: initial;
    max-width: none;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    padding: 0;
    margin: 0;
    background: #ccc;
  }

  .react-transform-component {
    background: $darkGray;
  }

  .tools {
    position: absolute;
    width: 100%;
    top: 5px;
    z-index: 150;
    background: transparent;
    text-align: right;

    button {
      margin-left: 3px;
      width: 24px;
      height: 24px;
      text-align: center;
      padding: 0;
      border: 2px solid #000;
    }
  }
}

.tools-slider {
  position: absolute;
  display: flex;
  align-items: center;
  width: calc(100% - #{$sideBarWidth + 15px});
  left: 0;
  top: 100%;
  height: $bottomPadding;
  z-index: 3000;
  padding: 0 40px;

  @media (max-width: 991px) {
    width: 100%;
  }

  &__zoom-out,
  &__zoom-in {
    display: block;
    width: 15px;
    height: 15px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: #FFF;
    font-size: 30px;
    background: transparent;
    border: 0;
    cursor: pointer;
    font-size: 0;
    background: no-repeat center center;
    background-size: contain;
  }

  &__zoom-out {
    background-image: url('#{$rootPath}/img/img-zoom-out.svg');
    left: 0;
  }

  &__zoom-in {
    background-image: url('#{$rootPath}/img/img-zoom-in.svg');
    right: 0;
  }
}

.tools-slider__wrapper {
  width: 100%;
  height: 25px;
}

.tools-slider__body {
  width: 100%;
  height: 25px;
}

.tools-slider__track {
  top: 50%;
  height: 1px;
  margin-top: -0.5px;
  background: rgba($lightGray, .2);
}

.tools-slider__thumb {
  width: 8px;
  height: 8px;
  top: 50%;
  margin-top: -4px;
  border-radius: 9999px;
  font-size: 0;
  background: #E0E0E0;
  outline: none;
}

.fullscreen-btn {
  .is-fullscreen & {
    background: #000;
    color: #FFF;
  }
}

.custom-button {
  margin: 0 3px;
  border: 2px solid #222;
  background: transparent;
}


.sandtable__sitetitle {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 21px;

  &:after {
    content: '';
    display: block;
    width: 80px;
    height: 1px;
    background: $darkGray;
    margin-left: 5px;
  }
}

.sandtable-interact-panel {
  position: relative;
  width: 100%;
  height: calc(100% - #{$topOffset});

  @media (max-width: 991px) {
    height: auto;
  }
}

.sandtable-msg {
  width: 100%;
  height: 100%;
  position: absolute;
  left: -$sideBarWidth;
  bottom: -$topPadding;
  background: $mediumGray;
  padding: $bottomPadding $sideBarWidth;
  z-index: 10;
  text-align: left;
  transition: left .4s;

  .sandtable__board:hover + .sandtable__control &{
    @media (min-width: 992px) {
      left: -20px;
    }
  }

  @media (max-width: 991px) {
    position: relative;
    left: 0;
    bottom: auto;
    padding: 45px 40px;
    // max-height: 50vh
  }

  // fade in
  animation: msg-fadein .4s forwards 2.9s;
  opacity:0;
  transform: translateY(60px);
  pointer-events: none;

  @keyframes msg-fadein {
    0% {
      opacity: 0;
      transform: translateY(60px);
      pointer-events: none;
    }

    100% {
      opacity: 1;
      transform: translateY(0);
      pointer-events: all;
    }
  }

  .sandtable-msg__msgs,
  p {
    // padding:20px;
    // border:2px solid #000;
    background: url('#{$rootPath}/img/img-notes-line.png') repeat;
    background-size: auto 50px;
    margin: 0;
    word-break: break-all;
    color: #E0E0E0;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 50px;
    height: 300px;
    overflow: auto;

    em {
      font-style: normal;
      font-weight: 400;
      color: $darkGray;
    }

    .sandtable-msg__dash {
      display: inline-block;
      vertical-align: middle;
      width: 50px;
      height: 1px;
      background: #E0E0E0;
    }
  }
}

.sandtable-msg__title {
  font-size: 21px;
  letter-spacing: 1px;
  margin-bottom: 90px;
  font-weight: 400;
  @media (max-width: 991px) {
    margin-bottom: 45px;
    font-size: 18px;
  }
}

.sandtable-msg-input {
  height: 50px;
  position: absolute;
  width:calc(100% - #{$sideBarWidth * 2});
  bottom: 70px;
  left: $sideBarWidth;
  display: flex;
  align-items: center;
  justify-content: center;
  background: $lightGray;
  padding: 0 15px;
  @media (max-width: 991px) {
    position: relative;
    bottom: auto;
    left: auto;
    width:100%;
    margin-top:45px;
  }

  input {
    width: 100%;
    border: 0;
    // border-bottom:2px solid #222;
    background: transparent;
    text-align: left;
    font-weight: 100;
    margin-right: 5px;
  }

  button {
    border: 0;
    background: transparent;
    font-size: 0;
    width: 42px;
    height: 100%;
    flex: 0 0 auto;
    padding: 0;
    background: url('#{$rootPath}/img/img-submit-arrow.svg') no-repeat center center;
    background-size: contain;
    transition: .4s;
    cursor: pointer;

    &:hover {
      transform: translateX(3px);
      opacity: .9;
    }
  }
}

.sandtable-board__zoomer {
  position: relative;
  width: 480px;

  &:before {
    content: '';
    display: block;
    padding-bottom: 75%;
  }

  .is-fullscreen & {
    width: 100vw;
    height: 100vh;
  }

  .is-halfscreen & {
    width: var(--sand-w);
    height: var(--sand-h);
  }
}

.sandtable-board__body {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0.25, 0.25);
  transform-origin: left top;
}

.sandtable-options {
  h3 {
    margin-bottom: 10px;
  }

  h4 {
    margin: 5px 0;
  }
}

.sandtable-option-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sandtable-msg-textarea {
  display: block;
  padding: 10px;
  border: 0;
  border: 2px solid #222;
  background: transparent;
  text-align: center;
  font-weight: 100;
  margin-bottom: 10px;
  resize: none;
  width: 100%;
}

.sandtable-about {
  width: 100%;
  height: 100%;
  position: absolute;
  left: -$sideBarWidth * 2;
  bottom: -$topPadding - $sideBarWidth;
  background: $lightGray;
  z-index: 30;
  padding: $sideBarWidth;
  padding-bottom: $sideBarWidth + $topPadding + $sideBarWidth;
  text-align: left;
  opacity: 0;
  transform: translateY(60px);
  pointer-events: none;
  transition: .4s;

  > div {
    height:100%;
  }

  &.is-active {
    opacity: 1;
    transform: translateY(0);
    pointer-events: all;
  }

  @media (max-width: 991px) {
    padding: 40px;
    opacity: 1;
    transform: translateY(0);
    pointer-events: all;
    position: relative;
    left: auto;
    bottom: auto;
    height: auto;
  }
}

.sandtable-about__title {
  // font-family: MSungHK-Medium, serif;
  font-size: 21px;
  letter-spacing: 1px;
  margin-bottom: 90px;
  font-weight: 400;
  @media (max-width: 991px) {
    margin-bottom: 40px;
    font-size: 18px;
  }
}

.sandtable-about__desc {
  font-size: 18px;
  color: #3C3C3C;
  letter-spacing: 0;
  line-height: 28px;
  @media (max-width: 991px) {
    font-size: 16px;
  }
}

.sandtable__sidebar {
  z-index: 30;
  position: absolute;
  right: 0;
  bottom: 0;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 $bottomPadding;
  width: 100vh;
  height: $sideBarWidth;
  transform: translateX(-#{$sideBarWidth}) rotate(90deg);
  transform-origin: right bottom;

  .sandtable__control &{
    background: #C19876;
  }
  @media (max-width: 991px) {
    position:relative;
    justify-content: space-between;
    right:auto;
    bottom:auto;
    transform: none;
    width: 100%;
    height: auto;
    padding: 20px 40px;
  }
}
.sandtable__mobile-menu{
  display:none;
  @media (max-width: 991px) {
    display: block;
  }
}

.sandtable__sidelink {
  background: transparent;
  text-decoration: none;
  border: 0;
  padding: 0;
  margin-right: 40px;
  font-size: 18px;
  color: $darkGray;
  letter-spacing: 0;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:last-child {
    margin-right: 0;
  }
}

.sandtable-scrollpane{
  overflow: auto;
  height: 100%;
}