
// Fonts Size (legacy):
// 20201226 - tried to use px for most type settings to be accurate
// Type settings in px can be found in the below of this file 
$fontSizeTitleXXL:10rem;
$fontSizeTitleXL:6.5rem;
$fontSizeTitleL:4rem;
$fontSizeTitleM:3rem;
$fontSizeTitleS:2.5rem;
$fontSizeTitleSS:1.3rem;
$fontSizeTitleXS:1rem;

$fontSizeXXL:2.5rem;
$fontSizeXL:2rem;
$fontSizeL:1.5rem;
$fontSizeM:1.3rem;
$fontSizeS:1rem;
$fontSizeSS:.9rem;
$fontSizeXS:.5rem;


// Font settings
@font-face {
    font-family: 'Neue Machina';
    src: url('#{$rootPath}/fonts/neue-machina/NeueMachina-Regular.eot');
    src: local('Neue Machina Regular'), 
        local('NeueMachina-Regular'), url('#{$rootPath}/fonts/neue-machina/NeueMachina-Regular.eot?#iefix') format('embedded-opentype'), 
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Regular.woff2') format('woff2'), 
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Regular.woff') format('woff'), 
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Regular.ttf') format('truetype'),
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Regular.svg#NeueMachina-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Neue Machina';
    src: url('#{$rootPath}/fonts/neue-machina/NeueMachina-Light.eot');
    src: local('Neue Machina Light'), local('NeueMachina-Light'), 
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Light.eot?#iefix') format('embedded-opentype'),
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Light.woff2') format('woff2'),
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Light.woff') format('woff'),
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Light.ttf') format('truetype'),
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Light.svg#NeueMachina-Light') format('svg');
    font-weight: 300;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Neue Machina';
    src: url('#{$rootPath}/fonts/neue-machina/NeueMachina-Ultrabold.eot');
    src: local('Neue Machina Ultrabold'), local('NeueMachina-Ultrabold'), 
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Ultrabold.eot?#iefix') format('embedded-opentype'), 
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Ultrabold.woff2') format('woff2'),
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Ultrabold.woff') format('woff'),
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Ultrabold.ttf') format('truetype'),
        url('#{$rootPath}/fonts/neue-machina/NeueMachina-Ultrabold.svg#NeueMachina-Ultrabold') format('svg');
      font-weight: 800;
      font-style: normal;
  }

$fontPrimary: "Neue Machina", -apple-system,
BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
"Fira Sans", "Droid Sans", "Helvetica Neue",
"Microsoft JhengHei", 微軟正黑體,
sans-serif;

// Type settings
@mixin secTitleTypo() {
    // font-size:100px;
    font-size:90px;
    line-height: 1em;
    @include largeScreen(){
        // font-size:$fontSizeTitleL + 1.5;
        font-size:70px;
    }
    @include tabletView(){
        // font-size:$fontSizeTitleL;
        font-size:52px;
    }
    @include mobileView(){
        // font-size:$fontSizeTitleM;
        font-size:46px;
    }
}
@mixin secSubtitleTypo() {
    font-weight: 700;
    // font-size:20px;
    // line-height: 24px;
    font-size:28px;
    line-height: 40px;
    letter-spacing: 1px;
    @include smallScreen(){
        font-size:15px;
        line-height: 20px;
        letter-spacing: 1px;
    }
}

@mixin secRotatingTextTypo() {
    font-size:75px;
    @include largeScreen(){
        // font-size:$fontSizeTitleL + 1.5;
        font-size:60px;
    }
    @include tabletView(){
        // font-size:$fontSizeTitleL;
        font-size:42px;
    }
    @include mobileView(){
        // font-size:$fontSizeTitleM;
        font-size:40px;
    }
}
@mixin secDescTypo() {
    // font-size:$fontSizeM;
    font-weight: 400;
    font-size: 24px;
    letter-spacing: 0;
    line-height: 34px;
    @include mobileView(){
        font-size: 18px;
        line-height: 26px;
    }
}


@mixin artisanDescTypo() {
