@import "../../styles/AppDesign.scss";

.main-layout {
  overflow:auto;
  width:100%;
  height: 100vh;
  @media (max-width: 991px) {
    overflow:visible;
    height: auto;
  }
}
.main-layout__body{
  width:100%;
  // min-width:1024px;
  min-height: 100%;
  // background:$main;
  background: $bgMain;
  text-align: center;
  overflow:hidden;
}
.main-layout__content{
  width:90%;
  max-width: 960px;
  margin:0 auto;
  text-align: center;
}
.main-layout__section-spacer{
  display:block;
  @include secSpacer();
}