@import "../../styles/AppDesign.scss";

.pattern-creater {
  display:block;
  position: relative;
  width:100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.pattern-creater__board{

  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 660px;
  height: 660px;

  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    z-index: 0;
    background: url(/img/pattern-template-sqaure.jpg) no-repeat center center;
    background-size: contain;
    opacity:0.5
  }

  button{
    position: relative;
    display:block;
    width: 20px;
    height: 20px;
    flex: 0 0 auto;
    padding:0;
    font-size:0;
    background:transparent;
    border:1px solid rgba(#000, 0.1);
    box-shadow: none;
    z-index: 10;
    &:hover{
      background: rgba(red, 0.2);
    }
    &.is-active{
      background: red;
    }
  }
}
.pattern-creater__result{
  width: 660px;
  margin-top:60px;
  font-size: 10px;
  word-break: break-all;
}