@import "../../styles/AppDesign.scss";
.ip-slideshow-wrapper,
.ip-slideshow {
	position: relative;
	// background: #000;
  canvas {
    background: $darkGray;
  }
}

.ip-nav-left,
.ip-nav-right {
  // display:none;
	width: 50px;
	height: 50px;
	top: 50%;
	margin-top: -25px;
	z-index: 100;		
	position: absolute;
	border: 2px solid #000;
	border-radius: 50%;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	transition: all 0.3s;	
	background-color: rgb(255, 165, 0);
}
			
.ip-nav-left {
	right: 100%;
	margin-right:20px;
	-webkit-transform: translateX(-100%);
	-moz-transform: translateX(-100%);
	transform: translateX(-100%);
	opacity: 0;
}
				
.ip-nav-right {
	left: 100%;
	margin-left:20px;
	-webkit-transform: translateX(100%);
	-moz-transform: translateX(100%);
	transform: translateX(100%);
	opacity: 0;
}

.ip-nav-left:hover, 
.ip-nav-right:hover {		
	background-color: rgb(255, 165, 0);
	cursor: pointer;
}

.ip-nav-left:after,
.ip-nav-right:after {
	width: 100%;
	height: 100%;
	color: #fff;
	font-family: 'Lato', sans-serif;		
	font-size: 36px;
	line-height: 45px;
	text-align: center;
	position: absolute;
	top: 0;
	left: 0;
}
				
.ip-nav-left:after {		
	content: '<';						
}
				
.ip-nav-right:after {		
	content: '>';
}
	
.ip-nav-show {			
	-webkit-transform: translateX(0%);
	-moz-transform: translateX(0%);
	transform: translateX(0%);
	opacity: 1;			
}