@import "../../styles/AppDesign.scss";

.loader-dots{
  display: inline-flex;
  vertical-align: middle;
  align-items: center;
  margin-left: 3px;
  pointer-events: none;
  opacity: 0;
  transition: .4s;
  &.is-show {
    opacity: 1;
  }
}
.loader-dots__dot{
  width:6px;
  height:6px;
  display:block;
  margin:0 1px;
  border-radius: 9999px;
  background: #E0E0E0;

  animation: loader-dot 1.5s infinite;
  @keyframes loader-dot {
    0% {
      opacity:0;
      transform: translateY(10px);
    }

    40% {
      opacity: 0.75;
      transform: translateY(0);
    }

    100% {
      opacity: 0.75;
      transform: translateY(0);
    }
  }
  &:nth-child(1) {
    animation-delay: 0s;
  }
  &:nth-child(2) {
    animation-delay: .2s;
  }
  &:nth-child(3) {
    animation-delay: .4s;
  }
  &:nth-child(4) {
    animation-delay: .6s;
  }
}
