@mixin fontFace($family, $src, $style: normal, $weight: normal) {
  @font-face {
    font-family: $family;
    src: url("#{$src}.otf") format("opentype");
    // src: url("#{$src}.eot"); // IE9 compat
    // src: url("#{$src}.eot?#iefix") format("embedded-opentype"),
    //   // IE8 and below
    //     url("#{$src}.woff") format("woff"),
    //   // standards
    //     url("#{$src}.ttf") format("truetype"),
    //   // Safari, Android, iOS
    //     url("#{$src}.svg##{$family}") format("svg"); // legacy iOS
    font-style: $style;
    font-weight: $weight;
  }
}

@mixin a11y-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

@mixin global-content-grid() {
  max-width: $gridMedium;
  width: 90%;
}

@mixin desktop-content-grid() {
  max-width: 560px;
  width: 100%;
}

@mixin sec-grid() {
  width: 50%;

  @include mobileView() {
    width: 100%;
  }
}

@mixin resetBtn() {
  appearance: none;
  border: 0;
  box-shadow: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
}

@mixin gray-card() {
  width: 100%;
  background-color: $dark;
  padding: 1rem;
  border-radius: 0.7rem;
  max-width: 480px;
}

@mixin centerXY() {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

@mixin secDesc {
  @include secDescTypo();
  position: relative;
  width: 75%;
  left: 50%;
  @include tabletView() {
      width: 100%;
      left: 0;
  }
}

@mixin secPadding() {
  padding-top: 45px;
  padding-bottom: 45px;
  @include tabletView() {
      padding-top: 30px;
      padding-bottom: 30px;
      padding-left:60px;
      padding-right:30px;
  }
}
@mixin secSpacer() {
  padding-top:75px;
  padding-bottom:75px;
  @include mobileView() {
      padding-top: 30px;
      padding-bottom: 30px;
  }
}

@mixin navAndSloganTopPadding($offset: 0, $offsetTb: 0, $offsetMb: 0) {
  padding-top: 150px + $offset;
  @include tabletView() {
    padding-top: 145px + $offsetTb;
  }
  @include mobileView() {
    padding-top: 145px + $offsetMb;
  }
  @media (max-width: 767px) and (max-height: 580px) {
    padding-top: 115px + $offsetMb;
  }
}